@import url(https://cdn.jsdelivr.net/npm/victormono@latest/dist/index.min.css);
@import url(https://cdn.jsdelivr.net/gh/aymanbagabas/iosevka-fonts@v6.1.2/dist/iosevka/iosevka.min.css);
html,body,#root{margin:0 auto;height:100%;color:#fff;background-color:#181818;scroll-behavior:smooth}a{color:#fff}.background{background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover;background-image:url(/react-portfolio/static/media/bg.0447e7ca.gif);-webkit-filter:blur(5px) brightness(50%);filter:blur(5px) brightness(50%);width:100%;height:100%}.about-container{box-sizing:border-box;margin:0 auto;margin-top:1.5em;margin-bottom:2.5em;width:100%;max-width:1100px;position:relative}.box-container{margin-bottom:2.5em;padding:2em;position:relative}.box-container h1{font-size:1.5em;margin:1em 0 1em 0}.fullscreen{height:100%;width:100%}.me{display:flex;align-items:center;height:auto;padding:1rem 0;z-index:10}.image-title{border-radius:100%;margin-right:.25em;height:9em;width:9em}.indent{margin-left:1.2em}.unset{flex-direction:unset;margin:0}.btn{-webkit-backface-visibility:hidden;backface-visibility:hidden;border:0;box-sizing:border-box;cursor:pointer;display:inline-block;font-size:16px;height:38px;line-height:38px;margin:.5em;padding:0 30px;text-align:center;text-decoration:none;white-space:nowrap}.hover{transition:transform .2s ease}.hover:hover{-webkit-filter:none !important;filter:none !important;transform:translateY(-0.5em)}.shake{animation:shake .5s;animation-iteration-count:infinite;animation-timing-function:ease-in-out;animation-fill-mode:forwards}@keyframes shake{0%,20%,50%,80%,100%{transform:translateX(0)}40%{transform:translateX(-5px)}60%{transform:translateX(5px)}}.undertitle{text-align:left;font-size:2.5rem;letter-spacing:1px;margin:0}.iosevka{font-family:"Iosevka",sans-serif}.victor-mono{font-family:"Victor Mono",monospace}.victor-mono-i{font-family:"Victor Mono",monospace;font-style:italic}
